@import url(https://fonts.googleapis.com/css?family=Noto+Sans);
/* Reset Css */
a {
  color: inherit; /* 부모 color색 상속 */
  text-decoration: none;
}

button,
input,
textarea {
  font-family: 'DM Sans', sans-serif;
  font-size: 16px;
}

button:focus,
button:active,
input:focus,
input:active,
textarea:focus,
textarea:active {
  outline: none;
  box-shadow: none;
}

ul,
ol,
li {
  list-style-type: none;
  padding-left: 0;
  margin-left: 0;
}

p {
  font-size: 16px;
  line-height: 1.5;
  color: #2b292d;
  letter-spacing: -0.01em;
}

.fill-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 140px;
  height: 48px;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.6;
  letter-spacing: -0.05em;
  color: #fff;
  background-color: #3040c4;
  border-radius: 2px;
  transition: opacity 300ms ease-in-out;
}

.fill-button.inverted {
  color: #3040c4;
  background-color: #fff;
}

.fill-button:hover {
  opacity: .5;

}

/* common */
body {
  margin: 0;
  font-family: 'Noto Sans', sans-serif;
  background-color: #15141F;
}

.font-white {
  color: #f0f4f5;
}

.gridCard img {
  width: 300px; 
  height: 400px;
  margin: 20px;
  border-radius: 4px;
}

/* movieList */
.movieList__area {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* movieDetail */
.movieDetail__imgArea {
  height: 400px;
  background-repeat: no-repeat;
  overflow:hidden;
  margin: 0 auto ;
}

.movieDetail__imgArea img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* movieInfo */
.movieInfo__wrapper {
  margin-top : 30px;
  display: grid;
  grid-template-columns: 16% 16% 16% 16% 16% 16%;
  grid-template-rows: 50px 50px 50px;
  justify-content: center;
  text-align: center;

}

.movieInfo__wrapper div{
  border: 1px solid #3e3947;
  border-collapse: separate;
  border-radius: 2px;
  line-height: 50px;
}

.movieInfo__wrapper :nth-child(odd) {
  background-color: #484157;
  color: #d7cee0;
}

.movieInfo__wrapper :nth-child(even) {
  background-color: #d7cee0;
}

.movieInfo__wrapper :nth-child(10) {
  grid-column: 4/7;
}

@media screen and (max-width: 768px){
  .movieInfo__wrapper {
    grid-template-columns: 40% 60%;
    grid-template-rows: 50px 50px 50px 50px 50px 50px 50px 50px;
  }

  .movieInfo__wrapper :nth-child(10) {
    grid-column: 2/3;
  }
}


:root {
  --text-color: #f0f4f5;
  --background-color: #15141F;
  --accent-color: #d49466;
}

/* nav */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #211F30;
  padding: 8px 12px;
}

.navbar__logo {
  font-size: 24px;
  color: #f0f4f5;
  color: var(--text-color);
}

.navbar__logo a {
  color: #f0f4f5;
}

.navbar__menu {
  display: flex;
  list-style: none;
  padding-left: 0;
  color: #f0f4f5;
  color: var(--text-color);
}

.navbar__menu li {
  padding: 8px 12px;
}

.navbar__menu li:hover,
.navbar__account a:hover{
  background-color: #393552;
  border-radius: 4px;
}

.navbar__icons {
  list-style: none;
  color: #f0f4f5;
  color: var(--text-color);
  display: flex;
  padding-left: 0;
}

.navbar__icons li {
  padding: 8px 12px;
}

.navbar__icons li:hover{
  background-color: #393552;
  border-radius: 4px;
  cursor:pointer;
}

.navbar__toggleBtn {
  display: none; 
  position: absolute;
  right: 15px;
  font-size: 28px;
  background-color: #211F30;
  border: none;
  cursor: pointer;
}

.navbar__toggleBtn i {
  content: '';
  display: inline-block;
  width: 30px;
  height: 30px;
  background: url(data:image/png;base64,) no-repeat center center;
}

.navbar__account {
  display: flex;
  color: #f0f4f5;
  color: var(--text-color);
}

.navbar__account a {
  padding: 8px 12px;
  margin-right: 10px; 
}


@media screen and (max-width: 768px){
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 8px 24px;
  }

  .navbar__menu {
    display: none;
    flex-direction: column;
    align-items: center;
    width:100%;
    z-index: 1;
  }

  .navbar__menu li {
    width: 100%;
    text-align: center;
  }

  .navbar__account {
    display: none;
    justify-content: center;
    width: 100%;
  }

  .navbar__toggleBtn {
    display: block;
  }
  
  .navbar__menu.active,
  .navbar__account.active {
    display: flex;
  }

  .movieList__area .movieList {
    flex-direction: column;
    align-items: center;
  }
  
}
